import React from 'react'
import AdminNavbar from './AdminNavBar';
import Alert from 'react-bootstrap/Alert';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Link} from 'react-router-dom'
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import 'bootstrap/dist/css/bootstrap.min.css';
import './adminDashboardX.css';
import homeIcon from "../AdminAssests/home-icon.svg";
import logout from "../AdminAssests/logout.png";
import theHunt from "../AdminAssests/archer.png";
import getHunt from "../AdminAssests/father-son-love.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Table from 'react-bootstrap/Table';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from 'react';
import Spinner from 'react-bootstrap/Spinner';


export default function AdminDashboard(props) {
        const [show, setShow] = useState(false);
        const handleClose = () => setShow(false);
        const handleShow = () => setShow(true);
        const [loading, setLoading] = useState(false)
        const [pending_transactions, setPending_transactions] = useState('')
        const [successful_transactions, setSuccessful_transactions] = useState('')
        const [total_transactions, setTotal_transactions] = useState('')
        const [users, setUsers] = useState('')
        const [total, setTotal] = useState('')
        const navigate = useNavigate()

                useEffect( () =>{
                        // Make a request for a user with a given ID
        if (sessionStorage['Admin-Token']){
                        axios.get('https://crackitfindit.rad5.com.ng/api/dashboard', { 
                        headers: {
                                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                                Accept: 'application/json'
                        }

                        })
                        .then(function (response) {
                        // handle success
                        console.log(response)
                        setLoading(true)
                        setTotal(response.data.data.total)
                        setPending_transactions(response.data.data.pending_transactions)
                        setSuccessful_transactions(response.data.data.successful_transactions)
                        setTotal_transactions(response.data.data.total_transactions)
                        setUsers(response.data.data.users)
                        })
                        .catch(function (error) {
                        // handle error
                        console.log(error);
                        })
                        .then(function () {
                        // always executed
                        });
        }
        else{
                navigate('/admin')
                
            } 

                }, [])

                const regUser = (e) =>{
                        e.preventDefault()
                        navigate('/users')      

                }
                const totalTransaction = (e) =>{
                        e.preventDefault();
                        navigate('/total-transaction')
                }
                const logoutFunction = (e) => {
                        e.preventDefault();
                        navigate('/admin')
                        sessionStorage.clear()
                }

  return (
<div>

    {loading ? 
    <div className='admin-dashboard-div'>
        <Container fluid>
                <Row>        
               
                        <Col md = {1} className = 'admin-dashboard-offcanvas'>
                        <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" />
                                <Offcanvas show={show} onHide={handleClose} responsive="lg">
                                        <Offcanvas.Header closeButton>
                                        <Offcanvas.Title></Offcanvas.Title>
                                        </Offcanvas.Header>
                                        <Offcanvas.Body>
                                        <Col className = "side-bar" >
                                        <Row>
                    <Link  to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
                    </Row>   
                    <Row>
                    <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
                    </Row>  
                    <Row>
                    <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
                    </Row>  
                    <Row>
                    <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
                    </Row>  
                    <Row>
                    <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
                    </Row>  
                    <Row>
                    <Link to="/hunts-table"> <img src={getHunt} alt="" className='side-bar-icon'  /></Link>
                    </Row> 
                    <Row>
                    <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
                    </Row>
                                        </Col>
                                        </Offcanvas.Body>
                                </Offcanvas>
                        </Col>

                        <Col className='mt-3'>
                        <Row>
                                <Col className='text-center'>
                                <h4>Explore User Statistics</h4>
                                </Col>
                        </Row>
                                    <Row className=''>
                             
                                        <Col className='registered-users mb-3' md = {4} >
                                        <Card className = "card-admin card-admin-cursor shadow" onClick={regUser} >
                                        <Card.Body>
                                                <Card.Title  className = "card-text">Registered Users: {users}</Card.Title>
                                        </Card.Body>
                                        </Card>
                                        </Col>

                                        <Col className='successful-transaction  mb-3' md = {4}>
                                        <Card className='card-admin shadow' >
                                        <Card.Body>
                                        <Card.Title className = "card-text">Successful Transactions: {successful_transactions}</Card.Title>
                                        </Card.Body>
                                        </Card>
                                        </Col>

                                        <Col className='pending-transaction  mb-3' md = {4} >
                                        <Card className='card-admin shadow'>
                                        <Card.Body>
                                                <Card.Title className = "card-text">Pending Transactions: {pending_transactions}</Card.Title>
                                        </Card.Body>
                                        </Card>
                                        </Col>
                                                
                                        <Col className='total-transaction   mb-3' md = {4}>
                                        <Card className = 'card-admin shadow card-admin-cursor'>
                                        <Card.Body>
                                        <Card.Title onClick={totalTransaction} className = "card-text">Total Transactions: {total_transactions}</Card.Title>
                                        </Card.Body>
                                        </Card>
                                        </Col>

                                        <Col className='user  mb-3' md = {4} >
                                        <Card className = 'card-admin shadow'>
                                        <Card.Body>
                                                <Card.Title className = "card-text">Total Amount: {total}</Card.Title>
                                        </Card.Body>
                                        </Card>
                                        </Col>
                                             
                                    </Row>
                        </Col>
                </Row>  
        </Container>
    </div> 
    : 
    <Container className='spinner'>
                        <Row>
                            <Col className = "text-center">
                                <Spinner animation="grow" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </Spinner>
                            </Col>
                        </Row> 
                        </Container>
}
    </div>
  )
}
