import React from 'react'
import Table from 'react-bootstrap/Table';
import './usersX.css'
import {Link} from 'react-router-dom'
import AdminNavbar from './AdminNavBar';
import { useState, useEffect } from 'react';
import axios from 'axios';
import Spinner from 'react-bootstrap/Spinner';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import logout from "../AdminAssests/logout.png";
import Button from 'react-bootstrap/Button';
import homeIcon from "../AdminAssests/home-icon.svg";
import theHunt from "../AdminAssests/archer.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import getHunt from "../AdminAssests/father-son-love.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Form from 'react-bootstrap/Form';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { useNavigate } from "react-router-dom";


export default function Users(props) {
    let count = 1;
    const navigate = useNavigate()
    const [userArray, setUserArray] = useState([])
    const [loading, setLoading] = useState(false)
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const logoutFunction = (e) => {
        e.preventDefault();
        navigate('/admin')
        sessionStorage.clear()
}
    
    useEffect(() =>{
        axios.get('https://crackitfindit.rad5.com.ng/api/users', { 
            headers: {
                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                Accept: 'application/json'
            }
         })
        .then(function (response) {
            // handle success
            setLoading(true)
            console.log(response.data.data)
            setUserArray(response.data.data)
            
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
        .then(function () {
            // always executed
        });
    }, [])

    

  return (
    <div>

    {
    
    loading ?

    <div className='users-component'>
        <Container className='users-general-container' fluid>
            <Row>
                <Col md = {1} className = 'users-offcanvas'>
                            <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" />
                            <Offcanvas show={show} onHide={handleClose} responsive="lg">
                                    <Offcanvas.Header closeButton>
                                    <Offcanvas.Title></Offcanvas.Title>
                                    </Offcanvas.Header>
                                    <Offcanvas.Body>
                                    <Col >
                            <Row>
                            <Link  to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
                            </Row>   
                            <Row>
                            <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
                            </Row>  
                            <Row>
                            <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
                            </Row>  
                            <Row>
                            <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
                            </Row>  
                            <Row>
                            <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
                            </Row>
                            <Row>
                    <Link to="/hunts-table"> <img src={getHunt} alt="" className='side-bar-icon'  /></Link>
                    </Row>    
                            <Row>
                            <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
                            </Row>
                                    </Col>
                                    </Offcanvas.Body>
                            </Offcanvas>
                </Col>

                <Col>
                <Row>
                    <Col className='text-center mt-3'>
                        <h4>List of users</h4>
                    </Col>
                </Row>
                <Row>
                <Table striped bordered hover responsive className='table-users' size="sm" >
                            <thead>
                            <tr>
                                <th>S/N</th>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Gender</th>
                                <th>Phone</th>
                                <th>Age</th>
                                <th>Address</th>
                            </tr>
                            </thead>
                            <tbody>
                                {
                                    userArray.map((user) => {
                                        return(
                                            <tr>
                                            <td>{count++}</td>
                                            <td>{user.name}</td>
                                            <td>{user.email}</td>
                                            <td>{user.gender}</td>
                                            <td>{user.phone}</td>
                                            <td>{user.age}</td>
                                            <td>{user.address}</td>
                                        </tr>
                                        )
                            })         
                        }
                            </tbody>
                </Table>
                </Row>
                <Row className='text-center'>
                            
                <Col><Button variant="secondary" className='me-5 mb-3 disabled next-prev'>Prev</Button></Col>
                <Col><Button variant="secondary" className='ms-5 mb-3 disabled next-prev'>Next</Button></Col>
                
                </Row>
                   
                </Col>
            </Row>   
        </Container>
       

</div>
   :  <Container className='spinner'>
   <Row>
       <Col className = "text-center">
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
       </Col>
   </Row> 
   </Container>}

</div>
  
  )
}
