import React from 'react'
import { FlutterWaveButton, closePaymentModal } from 'flutterwave-react-v3';
import DashboardNavbar from './DashBoardNavbar'
import "./dashboard.css"
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import { useNavigate } from "react-router-dom";
import ListGroup from 'react-bootstrap/ListGroup';
import { Link } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import { useState, useEffect , useLocation} from 'react';
import hunt from "../Assests/hunt.jpg";
import axios from 'axios';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import address_icon from "../Assests/address.png";
import email_icon from "../Assests/email.png";
import phone_icon from "../Assests/phone.png";
import Button from 'react-bootstrap/Button';



export default function App(props) {
    const navigate = useNavigate();
    const [noHunt, setNoHunt ] = useState(false)
    const [waitHunt, setWaitHunt] = useState(false)
    const [sorted, setSorted] = useState([])
    const [showHuntCategory, setShowHuntCategory] = useState(false)
    const [huntArray, setHuntArray] = useState([])
    const [showHunt, setShowHunt] = useState(false)
    const [loading, setLoading] = useState(false)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [address, setAddress] = useState('')
    const [status, setStatus] = useState('')
    const [phone, setPhone] = useState('')
    const [amount, setAmount] = useState('')
    const [currency, setCurrency] = useState('')
    const [transaction_id, setTransaction_id] = useState('')
    const public_key = 'FLWPUBK_TEST-ebda9cc20d96a270f48c384632067e67-X';
    const api = 'https://crackitfindit.rad5.com.ng'

    useEffect( () =>{
    if (sessionStorage.Token){
         axios.get(api + '/api/user', { 
             headers: {
                 Authorization: "Bearer " + sessionStorage.Token,
                 Accept: 'application/json'
             }
          })
         .then(function (response) {
             // handle success
            //  console.log(response)
             setName(response.data.data.name)
             setAddress(response.data.data.address)
             setEmail(response.data.data.email)
             setPhone(response.data.data.phone)
             setStatus(response.data.data.payment.status)
             setAmount(response.data.data.payment.amount)
             setTransaction_id(response.data.data.payment.transaction_id)
             setCurrency(response.data.data.payment.currency)
             setLoading(true)
         })
         .catch(function (error) {
             // handle error
            //  console.log(error);
             navigate('/login')
         });

        axios.get(api + '/api/hunts', { 
            headers: {
                Authorization: "Bearer " + sessionStorage.Token,
                Accept: 'application/json'
            }
         })
        .then(function (response) {
            // handle success
            setWaitHunt(false)
            setHuntArray(response.data.data)
            setShowHuntCategory(true)
        })
        .catch(function (error) {
            // handle error
            console.log(error);
            // navigate('/login')
        });
        }
    else{
        navigate('/login')
    }    
}, [])    
    // const getHunts = (e) => {
    //     e.preventDefault()
    //     setWaitHunt(true)
    //     axios.get(api + '/api/hunts', { 
    //         headers: {
    //             Authorization: "Bearer " + sessionStorage.Token,
    //             Accept: 'application/json'
    //         }
    //      })
    //     .then(function (response) {
    //         // handle success
    //         setWaitHunt(false)
    //         setHuntArray(response.data.data)
    //         setShowHuntCategory(true)
    //     })
    //     .catch(function (error) {
    //         // handle error
    //         console.log(error);
    //         // navigate('/login')
    //     });
        
    // }
    const getHuntCategory = (e, category) =>{
        e.preventDefault()
        // setSorted(huntArray.filter((hunt)=>{
        //     return hunt.hunt_category_id === category
        // }))
        for (let hunt of huntArray){
            if (hunt.hunt_category_id === category){
                console.log('correct')
                setSorted(hunt)
                setNoHunt(false)
                setShowHunt(true)
                console.log(hunt)
            }
            else{
                console.log('incorrect')
                setNoHunt(true)
                setShowHunt(false)
            }
        }
        // if (huntArray.length !== 0){
        //     // setShowHunt(true)
        //     console.log(huntArray)
        // }
        // else {
        //     setNoHunt(true)
        // }
        

    }
   
    const closeHunts = (e) => {
        e.preventDefault()
        setShowHunt(false)
    }
    const config = {
     public_key: public_key,
     tx_ref: transaction_id,
     amount: amount,
     currency: currency,
     payment_options: 'card,mobilemoney,ussd',
     customer: {
       email: email,
       phone_number:phone,
       name: name,
     },
     customizations: {
       title: 'Crack It, Find It',
       logo: hunt,
     },
   };
 
   const fwConfig = {
     ...config,
     text: 'Make Payment',
     callback: (response) => {
       //  console.log(response);
       closePaymentModal() // this will close the modal programmatically
       navigate("/dashboard");
     },
     onClose: () => {
 
     },
   };
 
   return (
     <div>
        { loading ? 
     <div className='dashboard-div'>
        <DashboardNavbar />
        <Container className='card-container'>
            <Row>
                <Col lg ={6}>
                    <Row>
                        <Col>
                        <div className='intro'>
                            <h4>Welcome {name}</h4>
                            <p>This is your DashBoard</p>
                        </div>
                        </Col>
                        
                    </Row>

                    <Row>
                        <Col xs={12}>
                                <Card  className='card bg-white shadow'>
                                        {/* <Card.Img variant="top" src="https://photos.app.goo.gl/dxrGkzDJCqM4geRx6" /> */}
                                        <Card.Body>
                                            {status == 'pending'?<Alert className='dashboard-status-alert' key={'danger'} variant={'danger'}>
                                                    <b>Status:</b> Inactive {' '}
                                                <Alert.Link><FlutterWaveButton className='flutter-btn btn btn-sm' {...fwConfig}  /></Alert.Link>
                                                </Alert> : null
                    
                                            }
                                            
                                            <Card.Title>{name}</Card.Title>
                                            {/* <Link className='btn btn-primary' to="/flutter">Make Payment to Join Hunt</Link><br /> */}
                                        </Card.Body>
                                        <ListGroup className="list-group-flush">
                                            <ListGroup.Item><img className='dashboard-icon' src={phone_icon} alt = ''/> {phone}</ListGroup.Item>
                                            <ListGroup.Item> <img className='dashboard-icon' src={email_icon} alt = ''/>  {email}</ListGroup.Item>
                                            <ListGroup.Item><img className='dashboard-icon' src={address_icon} alt = ''/> {address}</ListGroup.Item>
                                        </ListGroup> 
                                        <Card.Body>
                                        </Card.Body>
                                    </Card>
                        </Col>
                    </Row>  
                </Col>
                { showHuntCategory ? <Col lg ={6} className = 'dashboard-hunt-column'>
                    <Row>
                        <Col>
                        <div className='intro'>
                            <h4>{name}, you can access all the hunts here</h4>
                            <p>Select a Hunt category below</p>
                        </div>
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12}>
                                <Card  className='card shadow'>
                                        <ListGroup className="list-group-flush">
                                            <ListGroup.Item><Button onClick={(e) => getHuntCategory(e, 1)} variant="primary" className=' bg-secondary dashboard-hunt-category btn-sm'>Hunt for the Day</Button></ListGroup.Item>
                                            <ListGroup.Item><Button onClick={(e) => getHuntCategory(e, 2)} variant="primary" className='bg-secondary btn-sm'>Hunt for the Week</Button></ListGroup.Item>
                                            <ListGroup.Item><Button onClick={(e) => getHuntCategory(e, 3)} variant="primary" className=' bg-secondary btn-sm'>Hunt for the Month</Button></ListGroup.Item>
                                            <ListGroup.Item><Button onClick={(e) => getHuntCategory(e, 4)} variant="primary" className=' bg-secondary btn-sm'>Hunt for the Year</Button></ListGroup.Item>
                                        </ListGroup> 
                                    </Card>
                        </Col>
                    </Row>  
                </Col> : null}
            </Row>
            {
                noHunt ? <Col md = {6}><Alert variant="danger" onClose={() => setNoHunt(false)} dismissible className='mt-5'>
                <p>
                  No hunt available in this category
                </p>
              </Alert></Col> : null
            }
           
            { showHunt ?
                <Row>
                    <Row>
                        <Col>
                            {
                             <div className='intro mt-5'>
                                <h4>{sorted.hunt_category_title}</h4>
                                </div>
                            }
                        </Col>
                    </Row>
                    
                    <Row> 
                   
                    { 
                       sorted.hunts.map(  (hunt) =>{
                            return  <Col lg = {6}>
                                <Card className='card shadow'>
                                    <Card.Body> 
                                        <Card.Title><b>{hunt.audience}</b></Card.Title>
                                    </Card.Body>
                                    <ListGroup className="list-group-flush">
                                        <ListGroup.Item><b>Title:</b> {hunt.title}</ListGroup.Item>
                                        <ListGroup.Item><b>Description:</b> {hunt.description}</ListGroup.Item>
                                        <ListGroup.Item><img src={hunt.image_url} width = '200' height='200' /></ListGroup.Item>
                                        <ListGroup.Item> {hunt.date_created.slice(0, 10)}</ListGroup.Item>
                                    </ListGroup> 
                                </Card>
                                <br />
                            </Col>
                        }  )
                        
}
                   
                    </Row>
               
            </Row> 
            :
            null
            }
                     
               


           

        </Container>
        
    </div>
                    : <Container className='spinner'>
                        <Row>
                            <Col className = "text-center">
                                <Spinner animation="grow" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </Spinner>
                            </Col>
                        </Row> 
                        </Container>

}
     </div>
     
   );
 }