import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import axios from 'axios';
import { useState, useEffect } from 'react';
import Table from 'react-bootstrap/Table';
import { useNavigate } from "react-router-dom";
import "./huntsTable.css"
import Card from 'react-bootstrap/Card';
import Spinner from 'react-bootstrap/Spinner';
import {Link} from 'react-router-dom'
import Alert from 'react-bootstrap/Alert';
import homeIcon from "../AdminAssests/home-icon.svg";
import logout from "../AdminAssests/logout.png";
import pendingTransaction from "../AdminAssests/pending.png";
import theHunt from "../AdminAssests/archer.png";
import getHunt from "../AdminAssests/father-son-love.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Offcanvas from 'react-bootstrap/Offcanvas';
import Button from 'react-bootstrap/Button';
import Moment from 'react-moment';
import AnchorLink from "react-anchor-link-smooth-scroll"
import { useRef } from "react";

export default function HuntsTable() {
    const inputElement = useRef();
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [show, setShow] = useState(false);
    const [showAlert, setShowAlert] = useState(false);
    const [editLoading, setEditLoading] = useState(false)
    const [huntDetails, setHuntDetails] = useState([]);
    const [showEditHunt, setShowEditHunt] = useState(false)
    const [message, setMessage] = useState('')
    const [huntTitle, setHuntTitle] = useState("")
    const [removeTable, setRemoveTable] = useState(false)
    const [huntid, setHuntid] = useState("")
    const [uploaded, setUploaded] = useState(false)
    const [huntCategory, setHuntCategory] = useState([]);
    const [huntImage_url, setHuntImage_url] = useState("")
    const [huntImageDisplay, setHuntImageDisplay] = useState("")
    const [huntCategoryArray, setHuntCategoryArray] = useState([]);
    const [huntAudience, setHuntAudience] = useState("")
    const [huntCategory_id, setHuntCategory_id] = useState("")
    const [huntDescription, setHuntDescription] = useState("")
    const [huntExpiration, setHuntExpiration] = useState("")
    const [loading, setLoading] = useState(false)
    const [loadingEditHunt, setLoadingEditHunt] = useState(false)
    const navigate = useNavigate()
    useEffect( () =>{
        if (sessionStorage['Admin-Token']){
            axios.get('https://crackitfindit.rad5.com.ng/api/get-hunts', { 
                        headers: {
                                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                                Accept: 'application/json'
                        }

                        })
                        .then(function (response) {
                            // handle success
                            // console.log(response)
                            setLoading(true)
                            setHuntDetails(response.data.data)
                        })
                        .catch(function (error) {
                            // handle error
                            console.log(error);
                            // navigate('/login')
                        });
        }
        else{
                navigate('/admin')
                
            } 
    }, [])

    const editHunt = (e, hello) =>{
        e.preventDefault()
        setRemoveTable(true)
        setLoadingEditHunt(true)
        axios.get('https://crackitfindit.rad5.com.ng/api/get-hunt/' + hello, { 
            headers: {
                    Authorization: "Bearer " + sessionStorage['Admin-Token'],
                    Accept: 'application/json'
            }
            })
            .then(function (response) {
                // handle success
                setLoadingEditHunt(false)
                console.log(response.data.data)
                setHuntid(response.data.data.hunt_id)
                setHuntCategory_id(response.data.data.hunt_category.id)
                setHuntCategory(response.data.data.hunt_category.title)
                setHuntTitle(response.data.data.title)
                setHuntImage_url(response.data.data.image_url)
                setHuntImageDisplay(response.data.data.image_url)
                setHuntAudience(response.data.data.audience)
                setHuntDescription(response.data.data.description)
                setHuntExpiration(response.data.data.expiration.slice(0, 10))
                setShowEditHunt(true)
                // window.location = '#about'
            })
            .catch(function (error) {
                // handle error
                console.log(error);
                // navigate('/login')
            });

            axios.get('https://crackitfindit.rad5.com.ng/api/hunt-categories', { 
                        headers: {
                                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                                Accept: 'application/json'
                        }

                        })
                        .then(function (response) {
                        // handle success
                        setHuntCategoryArray(response.data.data)
                        })
                        .catch(function (error) {
                        // handle error
                        console.log(error);
                        })
                        .then(function () {
                        // always executed
                        });
    }
    const editHuntGotten = (e) => {
        e.preventDefault()
        setEditLoading(true)
        let form = new FormData();
        form.append("category_id", huntCategory_id);
        form.append("title", huntTitle);
        form.append("description", huntDescription);
        form.append("audience", huntAudience);
        if (uploaded === true){form.append('image', huntImage_url); console.log('jj')}
        form.append("expiration", huntExpiration);
        form.append("hunt_id", huntid);
        console.log(huntid)
        console.log(huntImage_url)
        axios.post('https://crackitfindit.rad5.com.ng/api/add-or-edit-hunt', form,    {headers: {
            Authorization: "Bearer " + sessionStorage['Admin-Token'],
            Accept: 'application/json'
    }},)
    .then(function (response) {
    // handle success
    console.log(response.data.message)
    setEditLoading(false)
    setMessage(response.data.message)
    setShowAlert(true)
    })
    .catch(function (error) {
    // handle error
    console.log(error);
    setEditLoading(false)
    setMessage(error.response.data.message)
    setShowAlert(true)
    })
}
    const backDashboard = (e) =>{
        e.preventDefault();
        navigate('/hunt')
    }
    const handleFileChange = (event) => {
        event.preventDefault()
        setUploaded(true)
        console.log(uploaded)
        setHuntImageDisplay(URL.createObjectURL(event.target.files[0]))
        setHuntImage_url(event.target.files[0])
    }
    const goBack = (e) =>{
        e.preventDefault()
        setRemoveTable(false);
        setShowEditHunt(false)
    }
    const logoutFunction = (e) => {
        e.preventDefault();
        navigate('/admin')
        sessionStorage.clear()
}
  return (
    <>
    { 
    loading ?
    <div>
        {
            removeTable ?
            null
            :
            <Container fluid>
            <Row>
                <Col md = {1} className = 'huntsTable-offcanvas' >
                                    <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" /> 
                                    <Offcanvas show={show} onHide={handleClose} responsive="lg">
                                            <Offcanvas.Header closeButton>
                                            <Offcanvas.Title></Offcanvas.Title>
                                            </Offcanvas.Header>
                                            <Offcanvas.Body>
                                            <Col className = "side-bar" >
                                            <Row>
                        <Link  to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
                        </Row>   
                        <Row>
                        <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                            <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
    
                        <Row>
                        <Link to="/hunts-table"> <img src={getHunt} alt="" className='side-bar-icon'  /></Link>
                        </Row> 
                        
                        <Row>
                        <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
                        </Row>
                                            </Col>
                                            </Offcanvas.Body>
                                    </Offcanvas>
                </Col>
                <Col>
                    <Row>
                                    <Col className='text-center mt-1 hunt-title'>
                                        <h4>List of Hunts</h4>
                                    </Col>
                    </Row>
                    <Row>
                        <Col className='hunts-table-column'>
                            <Table striped bordered hover responsive className=''>
                                <thead>
                                <tr>
                                <th>hunt_id</th>
                                <th>hunt_category</th>
                                <th>title</th>
                                <th>description</th>
                                <th>audience</th>
                                <th>image_url</th>
                                <th>date_created</th>
                                <th>expiration</th>
                                <th>Edit</th>
                                </tr>
                                </thead>
                                <tbody>
                                {
                                huntDetails.map( (huntDetail) =>{
                                        return (
                                                <tr key = {Math.random()}>
                                                <td>{huntDetail.hunt_id}</td>
                                                <td>{huntDetail.hunt_category.title}</td>
                                                <td>{huntDetail.title}</td>
                                                <td>{huntDetail.description}</td>
                                                <td>{huntDetail.audience}</td>
                                                <td className='text-center'><img src={huntDetail.image_url} alt="" width = '50%' /></td>
                                                <td>{huntDetail.date_created.slice(0, 10)}</td>
                                                <td>{huntDetail.expiration.slice(0, 10)}</td>
                                                {/* <td><input type='button' id={Math.random()} ref = {inputElement} value='Edit Hunt' className='btn btn-primary huntsTable-edit-hunt-btn' onClick={ (e) => editHunt(e, huntDetail.hunt_id )} /></td> */}
                                                <td> <button onClick={ (e) => editHunt(e, huntDetail.hunt_id )} className='btn btn-primary huntsTable-edit-hunt-btn' id={Math.random()} ref = {inputElement}>Edit Hunt</button></td>
                                                </tr>  
                                        )
                                })
                                }
                                        
                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                    <Row className='text-center hunt-btn'>
                                        
                                        <Col><Button variant="secondary" className='me-5 mb-2  next-prev disabled'>Prev</Button></Col>
                                        <Col><Button variant="secondary" className='ms-5 mb-2 next-prev disabled'>Next</Button></Col>
                                        
                    </Row>
                </Col>
            </Row>
        </Container>
            
        }
         {loadingEditHunt ?
    <Container className='spinner' >
    <Row>
        <Col className = "text-center">
            <Spinner animation="grow" role="status">
                <span className="visually-hidden">Loading...</span>
            </Spinner>
        </Col>
    </Row> 
    </Container>
    :
    null }
  

    {/* EDIT HUNT */}
    {
        showEditHunt ?
    <Container fluid>
        <Row className='mt-3'>
        <Col md = {4}>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Category</Card.Title>
                            <select value={huntCategory} onChange={(e) => setHuntCategory(e.target.value)} >
                            <option value="" disabled>Select Category</option>
                            {huntCategoryArray.map((hunt) =>{
                                                                return <option value={hunt.title} key = {hunt.id} onClick={
                                                                        () =>{  setHuntCategory_id(hunt.id)
                                                                }}>
                                                                        {hunt.title}</option>
                                                        })}
                            </select>
                    </Card.Body>
                </Card>
            </Col>
            <Col md = {4} className = 'mb-3'>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Title</Card.Title>
                            <input type="text" onChange={(e) => {setHuntTitle(e.target.value)}} className = "huntsTable-hunt-input huntsTable-hunt-title" value={huntTitle} />
                    </Card.Body>
                </Card>
            </Col>

            <Col md = {4} className = 'mb-3'>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Audience</Card.Title>
                            <input type="text" onChange={(e) => {setHuntAudience(e.target.value)}} className = "huntsTable-hunt-input huntsTable-hunt-audience" value={huntAudience}/>
                    </Card.Body>
                </Card>
            </Col>
            <Col md = {4} className = 'mb-3'>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Expiration</Card.Title>
                            <input type="date" onChange={(e) => {setHuntExpiration(e.target.value); console.log(huntExpiration)} }className = "huntsTable-hunt-input" value={huntExpiration} />
                    </Card.Body>
                </Card>
            </Col>

            <Col md = {4} className = 'mb-3'>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Guide</Card.Title>
                            <img src={huntImageDisplay} alt="" width="50%"/>
                            <input type="file" onChange={handleFileChange} className = "huntsTable-hunt-guide mt-3" />
                    </Card.Body>
                </Card>
            </Col>
            <Col md = {4} className = 'mb-3'>
                <Card className=''>
                    <Card.Body>
                            <Card.Title className = "">Hunt Description</Card.Title>
                           <textarea name="" id="" cols="23" rows="5" className = "huntsTable-hunt-input" value={huntDescription} onChange={(e) => {setHuntDescription(e.target.value)}} ></textarea>
                    </Card.Body>
                </Card>
            </Col>
           
           
        </Row>
        <Row>
           <Col> <button className='btn btn-primary btn-sm mb-3' onClick={editHuntGotten}>Update Hunt</button></Col>
           <Col> <button className='btn btn-primary btn-sm mb-3' onClick={goBack}>Go back</button></Col>
           <Col>{editLoading ?  
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
    : null}
    {showAlert ?
                        <Alert variant="danger" onClose={() => setShowAlert(false)} dismissible className='mb-3'>
                        <p>
                        {message}
                        </p>
                        </Alert>
                        : null}
    </Col>
        </Row>
        
        
        
      
    </Container>
    :
    null
    }
   
</div>
:
<Container className='spinner'>
   <Row>
       <Col className = "text-center">
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
       </Col>
   </Row> 
   </Container>}
    </>
   
  )
}
