    // const [formInput, setFormInput] = useState({
    //                 password: "",
    //                 confirmPassword: "",
    // });
    

    const api = 'https://crackitfindit.rad5.com.ng'

    const handleSubmit = (event) => {
        event.preventDefault();
        setLoading(true)
        if (props.password === props.confirm_password){
         if (termsChecked){
          axios.post(api + '/api/user-reg', {
            'name': `${props.name}`,
            'gender': `${props.gender}`,
            'email': `${props.email}`,
            'phone': `${props.number}`,
            'password': `${props.password}`,
            'address': `${props.address}`,
            'age': `${props.age}`,
            'occupation': `${props.occupation}`,
            'nationality' : `${props.nation}`
          })
          .then(function (response) {
            setLoading(false)
            sessionStorage.setItem("Token", `${response.data.data.token}`);
            console.log(response.data);
            navigate("/dashboard");
          })
          .catch(function (error) {
            if (error.response){
              setLoading(false)
              console.log(error.response.data);
              setSuccess(error.response.data.success)
              setMessage(error.response.data.message)
            }
            else{
              setLoading(false)
              setSuccess(false)
              setNetwork( network = 'Encountered an error, Please try again')
              console.log('error')
            }
               
          });
         }
         else{
          setLoading(false)
          setSuccess(false)
          setMessage("You must agree to the terms and condition")
          console.log(message)
         }
        }
        else {
          setLoading(false)
          setSuccess(false)
          setMessage("Password and confirm password should be same")
          console.log(message)
        }
   
  
      }
