import React from 'react'
import ToggleButton from 'react-bootstrap/ToggleButton';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import AdminNavbar from './AdminNavBar';
import Dropdown from 'react-bootstrap/Dropdown';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Link} from 'react-router-dom'
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import 'bootstrap/dist/css/bootstrap.min.css';
import './huntX.css';
import homeIcon from "../AdminAssests/home-icon.svg";
import logout from "../AdminAssests/logout.png";
import pendingTransaction from "../AdminAssests/pending.png";
import getHunt from "../AdminAssests/father-son-love.png";
import theHunt from "../AdminAssests/archer.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Table from 'react-bootstrap/Table';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from 'react';
import Spinner from 'react-bootstrap/Spinner';
import Alert from 'react-bootstrap/Alert';



export default function Hunt(props) {
        const [showAlert, setShowAlert] = useState(false);
        const [huntArray, setHuntArray] = useState([]);
        const [publishLoading, setPublishLoading] = useState(false)
        const [show, setShow] = useState(false);
        const [image, setImage] = useState(null)
        const handleClose = () => setShow(false);
        const handleShow = () => setShow(true);
        const [loading, setLoading] = useState(false)
        const [category, setCategory] = useState("");
        const [audience, setAudience] = useState('')
        const [expiration, setExpiration] = useState('')
        const [title, setTitle] = useState('')
        const [description, setDescription] = useState('')
        const [category_id,setCategory_id] = useState('');
        const [message, setMessage] = useState('')
        const navigate = useNavigate()
       

                useEffect( () =>{
                        // Make a request for a user with a given ID
        if (sessionStorage['Admin-Token']){
                        axios.get('https://crackitfindit.rad5.com.ng/api/hunt-categories', { 
                        headers: {
                                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                                Accept: 'application/json'
                        }

                        })
                        .then(function (response) {
                        // handle success
                        setLoading(true)
                        setHuntArray(response.data.data)
                        })
                        .catch(function (error) {
                        // handle error
                        console.log(error);
                        })
                        .then(function () {
                        // always executed
                        });
        }
        else{
                navigate('/admin')
                
            } 

                }, [])

                const logoutFunction = (e) => {
                        e.preventDefault();
                        navigate('/admin')
                        sessionStorage.clear()
                }

                const onFileChange = (e) => {
                    setImage(e.target.files[0])
                }
                const handlePublish = (e) =>{
                        e.preventDefault()
                        setPublishLoading(true)
                        let form = new FormData();
                        form.append("category_id", category_id);
                        form.append("title", title);
                        form.append("description", description);
                        form.append("audience", audience);
                        form.append('image', image);
                        form.append("expiration", expiration);
                        axios.post('https://crackitfindit.rad5.com.ng/api/add-or-edit-hunt', form,    {headers: {
                                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                                Accept: 'application/json'
                        }},)
                        .then(function (response) {
                        // handle success
                        console.log(response.data.message)
                        setPublishLoading(false)
                        setMessage(response.data.message)
                        setShowAlert(true)
                        })
                        .catch(function (error) {
                        // handle error
                        console.log(error);
                        setPublishLoading(false)
                        setMessage(error.response.data.message)
                        setShowAlert(true)
                        })
                }
                const getHunts = (e) => {
                        e.preventDefault()
                        navigate('/hunts-table')
                    }
                const handleSelectChange = (e) => {
                        setCategory(e.target.value)
                        for (let hunt of huntArray){
                                if (e.target.value === hunt.title){
                                        setCategory_id(hunt.id)
                                }
                        }
                        console.log(category_id)
                }

  return (
        <div>
{
        loading  
        ?
<div className='hunt-dashboard-div'>
        <Container fluid>
            <Row> 
                <Col md = {1} className = 'hunt-offcanvas' >
                        <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" /> 
                        <Offcanvas show={show} onHide={handleClose} responsive="lg">
                                <Offcanvas.Header closeButton>
                                <Offcanvas.Title></Offcanvas.Title>
                                </Offcanvas.Header>
                                <Offcanvas.Body>
                                <Col className = "side-bar" >
                                <Row>
            <Link  to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
            </Row>   
            <Row>
            <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
            </Row>  
            <Row>
            <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
            </Row>  
            <Row>
            <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
            </Row>  
            <Row>
                <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
            </Row>   
            <Row>
                    <Link to="/hunts-table"> <img src={getHunt} alt="" className='side-bar-icon'  /></Link>
            </Row> 
            <Row>
            <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
            </Row>
                                </Col>
                                </Offcanvas.Body>
                        </Offcanvas>
                </Col>

                <Col className=''>
                <Row>
                    <Col className='text-center mt-3'>
                        <h4>Create Hunts</h4>
                    </Col>
                </Row>
                <Row className=''>      
                        <Col className='mb-3' lg = {6}>
                                <p>Hunt Category</p>
                                <select value={category} onChange={handleSelectChange} className ='hunt-input-text'>
                                        <option value="" disabled>Select Category</option>
                                        {
                                                huntArray.map((hunt) =>{
                                                        return <option value={hunt.title} onClick={
                                                                () =>{ setCategory(hunt.title);
                                                                        setCategory_id(hunt.id)
                                                        }}>{hunt.title}</option>
                                                })
                                        }
                                </select>
                        </Col>
                        <Col className='mb-3' lg = {6}>
                        <p>Hunt Title</p>
                        <input type="text" onChange={(e) => {setTitle(e.target.value)}}  value={title} className ='hunt-input-text'/>
                        </Col>
                        <Col className='mb-3' lg = {6}>
                                <p>Hunt Audience</p>
                        <input type="text" onChange={(e) => {setAudience(e.target.value)}}  value={audience} className ='hunt-input-text'/>
                        </Col>                        
                        <Col className='' lg = {6}>
                                <p>Hunt Expiration</p>
                        <input type="date" onChange={(e) => {setExpiration(e.target.value)}}  value={expiration} className ='hunt-input-text'/>
                        </Col>
                        <Col className='hunt-guide mb-3' lg = {6}>
                                <p>Hunt Guide</p>
                        <input type="file" onChange={onFileChange} className = "hunt-image-upload" />
                         </Col>
                         <Col className='hunt-description mb-3' lg = {6}>
                                <p>Hunt Description</p>
                        <textarea name="" id="" cols="30" rows="10" onChange={(e) => {setDescription(e.target.value)}} className ='hunt-textarea'></textarea>   
                        </Col>
                <Row>

                <Row>      
                        <Col lg={{ span: 6, offset: 6 }}>
                                <ToggleButtonGroup type="radio" name="options" defaultValue={1} srt>
                                <ToggleButton id="tbg-radio-1" value={1} onClick={handlePublish}>
                                        Publish
                                </ToggleButton>
                                </ToggleButtonGroup>
                        </Col>
                </Row>
                {publishLoading ? <Spinner animation="border" role="status" className='spinner-login '>
                        <span className="visually-hidden">Loading...</span>
                </Spinner> : null}
                {showAlert ?
                                <Alert variant="danger" onClose={() => setShowAlert(false)} dismissible>
                                <p>
                                {message}
                                </p>
                                </Alert>
                                : null}
                        </Row>            
                        
                </Row>
                </Col>
            </Row>
           


        </Container>

    </div> 
    :
    <Container className='spinner'>
   <Row>
       <Col className = "text-center">
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
       </Col>
   </Row> 
   </Container>

}
        </div>
    
  )
}
