import { BrowserRouter, Routes, Route } from "react-router-dom";
import { useState } from "react";
import Flutter from "./flutter";
import Dashboard from "./Dashboard/Dashboard";
import LandingPage from "./LandingPage"
import AdminLogin from "./Admin/AdminLogin";
import AdminDashboard from "./Admin/AdminDashboard/AdminDashboard";
import Form from "./Form/Form";
import Login from "./Login/Login";
import Users from "./Admin/AdminDashboard/Users";
import Transaction from "./Admin/AdminDashboard/Transaction";
import SignUp from "./Form/SignUp";
import FormX from "./Form/FormX";
import Hunt from "./Admin/AdminDashboard/Hunt";
import HuntsTable from "./Admin/AdminDashboard/HuntsTable";
import TransactionX from "./Admin/AdminDashboard/TransactionX";
import FormXV from "./Form/FormXV";
import FormTerms from "./Form/FormTerms";
const RouteSwitch = () => {
  const [name, setName] = useState("")
  const [number, setNumber] = useState("")
  const [email, setEmail] = useState("")
  const [address, setAddress] = useState("")
  const [age, setAge] = useState("")
  const [occupation, setOccupation] = useState("")
  const [password, setPassword] = useState("")
  const [confirm_password, setConfirm_password] = useState("")
  const [gender, setGender] = useState("")
  const [nation, setNation] = useState("")

  // const  handleChange = (e) => {
  //   setName(name = e.target.value)
  // };
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL} forceRefresh={true}>
      <Routes>
        <Route path="/admin" element={<AdminLogin />} />
        <Route path="/terms" element={<FormTerms />} />
        <Route path="/formxv" element={<FormXV />} />
        <Route path="/X" element={<TransactionX />} />
        <Route path="/hunt" element={<Hunt />} />
        <Route path="/hunts-table" element={<HuntsTable />} />
        <Route path="/formx" element={<FormX />} />
        <Route path="/signup" element={<SignUp />} />
        <Route path="/total-transaction" element={<Transaction />} />
        <Route path="/login" element={<Login />} />
        <Route path="/admin-dashboard" element={<AdminDashboard   />} />
         <Route path="/users" element={<Users
        />} />
        <Route path="/" element={<LandingPage />} />

        {/* Dashboard Route */}
        <Route path="/dashboard" element={<Dashboard
        name = {name} setName = {setName}
        number = {number  } setNumber = {setNumber}
        email = {email} setEmail = {setEmail}
        address = {address} setAddress = {setAddress}
        age = {age} setAge = {setAge}
        occupation = {occupation} setOccupation = {setOccupation}
        password = {password} setPassword =  {setPassword}
              confirm_password = {confirm_password} setConfirm_password = {setConfirm_password}
              gender = {gender} setGender = {setGender}
              nation = {nation} setNation = {setNation}
        
        />} />


        <Route path="/flutter" element={<Flutter  name = {name} setName = {setName}
              number = {number  } setNumber = {setNumber}
              email = {email} setEmail = {setEmail}
              address = {address} setAddress = {setAddress}
              age = {age} setAge = {setAge}
              occupation = {occupation} setOccupation = {setOccupation}
              password = {password} setPassword =  {setPassword}
              confirm_password = {confirm_password} setConfirm_password = {setConfirm_password}
              gender = {gender} setGender = {setGender}
              nation = {nation} setNation = {setNation}
        />} />

        
<Route path="/form" element={<Form  name = {name} setName = {setName}
              number = {number  } setNumber = {setNumber}
              email = {email} setEmail = {setEmail}
              address = {address} setAddress = {setAddress}
              age = {age} setAge = {setAge}
              occupation = {occupation} setOccupation = {setOccupation}
              password = {password} setPassword =  {setPassword}
              confirm_password = {confirm_password} setConfirm_password = {setConfirm_password}
              gender = {gender} setGender = {setGender}
              nation = {nation} setNation = {setNation}
        />} />
      </Routes>
    </BrowserRouter>
  );
};

export default RouteSwitch;