import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import YoutubeEmbed from "./YoutubeEmbed";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';


export default class Header extends Component {
  render() {
    return (
      <div className='header'>

            <Container fluid="md">
                  <Row className="justify-content-md-center">
                    <Col xs ={8}>
                        < div className='text-center header-col px-5'>
                          <h2>The Future of Hunting depends on the future of hunters</h2>
                        </div>
                    </Col>
                  </Row>
            </Container>

            <Container fluid="md">
                <Row className="justify-content-md-center">
                  <Col xs = {7}>
                  <div className='youtube text-center'><YoutubeEmbed embedId="rokGy0huYEA" /></div>
                  </Col>
                </Row>
                <Row className="justify-content-md-center">
                    <Col xs = {7}>
                          <div className='text-center mt-3'>
                                <p style={{color:"white"}}>
                                "Crack it, Find it" is a Daily/Weekly Virtual and Live Cerebral Hunt show where eligible Hunters are given specific Code to be Decoded at a given time . The enclosed Code should guide them to the Location of the Treasure. whoever Decodes on time and Gets to the Location and Finds the Treasure takes the reward Daily/ weekly respectively.
                                </p>

                          </div>
                    </Col>

                </Row>
                
            </Container>

            
        </div>
    )
  }
}
