import React, { Component } from 'react'
import Navbar from './Navbar/Navbar'
import Header from './Header/Header'
import Button1 from './Button/Button'
import './LandingPage.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import axios from 'axios';
import { useState, useEffect , useLocation} from 'react';
import ToggleButton from 'react-bootstrap/ToggleButton';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import Spinner from 'react-bootstrap/Spinner';
import Alert from 'react-bootstrap/Alert';
import { useRef } from "react";


export default function LandingPage(props){
    const inputElement1 = useRef();
    const inputElement2 = useRef();
    const inputElement3 = useRef();
    const inputElement4 = useRef();
    const arrInputElement = [inputElement1, inputElement2, inputElement3, inputElement4]
  const [showHunt, setShowHunt] = useState(false);
  const [noHunt, setNoHunt ] = useState(false);
  const [sorted, setSorted] = useState([]);
  const [huntArray, setHuntArray] = useState([]);
  const [waitHunt, setWaitHunt] = useState(true);
  const api = 'https://crackitfindit.rad5.com.ng';

  useEffect( () =>{
    axios.get(api + '/api/hunts', { 
        headers: {
            Authorization: "Bearer " + sessionStorage.Token,
            Accept: 'application/json'
        }
     })
    .then(function (response) {
        // handle success
        setWaitHunt(false)
        setHuntArray(response.data.data)
        inputElement1.current.style.backgroundColor = 'blue'
    })
    .catch(function (error) {
        // handle error
        console.log(error);
        // navigate('/login')
    });
    
}, [])    
//   const getHunts = (e, category, inputElement) => {
//     e.preventDefault()
//     // console.log(inputElement)
//     inputElement.current.style.backgroundColor = "blue";
//     setWaitHunt(true)
//     axios.get(api + '/api/hunts', { 
//         headers: {
//             Authorization: "Bearer " + sessionStorage.Token,
//             Accept: 'application/json'
//         }
//      })
//     .then(function (response) {
//         // handle success
//         setWaitHunt(false)
//         setHuntArray(response.data.data)
//         getHuntCategory(e,category)()
//     })
//     .catch(function (error) {
//         // handle error
//         console.log(error);
//         // navigate('/login')
//     });
    
// }
const getHuntCategory = (e, category, inputElement) =>{
    e.preventDefault()
    inputElement.current.style.backgroundColor = "blue";
    for (let element of arrInputElement){
        if (inputElement != element){
            element.current.style.backgroundColor = '#FE6323';
        }
    }
    for (let hunt of huntArray){
        if (hunt.hunt_category_id === category){
            console.log('correct')
            setSorted(hunt)
            setNoHunt(false)
            setShowHunt(true)
            console.log(hunt)
        }
        else{
            console.log('incorrect')
            setNoHunt(true)
            setShowHunt(false)
        }
    }
    

}
// const goDown =  (e) =>{
//     e.preventDefault()
//     for (let hunt of huntArray){
//         if (hunt.hunt_category_id === 4){
//             console.log('correct')
//             setSorted(hunt)
//             setNoHunt(false)
//             setShowHunt(true)
//             window.location.replace('#about')
//             console.log(hunt)
//         }
//         else{
//             console.log('incorrect')
//             setNoHunt(true)
//             setShowHunt(false)
//         }
//     }
   
// }
    return (
      <>
      {waitHunt ?
      
      <Container className='spinner'>
                        <Row>
                            <Col className = "text-center">
                                <Spinner animation="grow" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </Spinner>
                            </Col>
                        </Row> 
                        </Container>
      
      :
      
      <div className='body' >
      <Navbar />
      <Header />
      <Button1 />
      <Container>
        <Row className = 'mb-3'>
             <ButtonGroup aria-label="Basic example">
                <Button variant="secondary" className='landingpage-GetHuntButton' onClick={(e) => getHuntCategory(e, 1,inputElement1 )} ref={inputElement1}>Hunt for the Day</Button>
                <Button variant="secondary" className='landingpage-GetHuntButton' onClick={(e) => getHuntCategory(e, 2, inputElement2)} ref={inputElement2}>Hunt for the Week</Button>
                <Button variant="secondary" className='landingpage-GetHuntButton'  onClick={(e) => getHuntCategory(e, 3, inputElement3)} ref={inputElement3}>Hunt for the Month</Button>
                <Button variant="secondary" className='landingpage-GetHuntButton' onClick={(e) => getHuntCategory(e, 4, inputElement4)} ref={inputElement4}>Hunt for the Year</Button>
                {/* <input type="button" value="hhj" className='btn landingpage-GetHuntButton'/> */}
            </ButtonGroup>
        </Row>
        <Row>
        {
                noHunt ? <Col md = {6}><Alert variant="danger" onClose={() => setNoHunt(false)} dismissible className='mt-2'>
                <p>
                  No hunt available in this category
                </p>
              </Alert></Col> : null
            }
            {
                waitHunt ?  <Col className = "text-center ">
                <Spinner animation="grow" role="status" className='mt-5'>
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </Col> : null
            }
            { showHunt ?
                <Row id = 'about'>
                    <Row>
                        <Col>
                            {
                             <div className='landing-page-hunt-intro mt-3'>
                                <h4>{sorted.hunt_category_title}</h4>
                                </div>
                            }
                        </Col>
                    </Row>
                    
                    <Row> 
                   
                    { 
                       sorted.hunts.map(  (hunt) =>{
                            return <Col lg = {6}>
                            <Card className='card shadow'>
                                <Card.Body> 
                                    <Card.Title><b>{hunt.audience}</b></Card.Title>
                                </Card.Body>
                                <ListGroup className="list-group-flush">
                                    <ListGroup.Item><b>Title:</b> {hunt.title}</ListGroup.Item>
                                    <ListGroup.Item><b>Description:</b> {hunt.description}</ListGroup.Item>
                                    <ListGroup.Item><img src={hunt.image_url} width = '200' height='200' /></ListGroup.Item>
                                    <ListGroup.Item> {hunt.date_created.slice(0, 10)}</ListGroup.Item>
                                </ListGroup> 
                            </Card><br />
                            </Col>
                        }  )
                        
}
                                       
                    </Row>
               
            </Row> 
            :
            null
            }
        </Row>
      </Container>
     
      
      </div>

      }
      </>
    )

}
