import React from 'react'
import Table from 'react-bootstrap/Table';
// import './transaction.css'
import {Link} from 'react-router-dom'
import AdminNavbar from './AdminNavBar';
import { useState, useEffect } from 'react';
import axios from 'axios';
import Spinner from 'react-bootstrap/Spinner';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import logout from "../AdminAssests/logout.png";
import homeIcon from "../AdminAssests/home-icon.svg";
import pendingTransaction from "../AdminAssests/pending.png";
import theHunt from "../AdminAssests/archer.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Form from 'react-bootstrap/Form';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { useNavigate } from "react-router-dom";

export default function Transaction(props) {
    const navigate = useNavigate()
    const [transactionArray, setTransactionArray] = useState('')
    const [loading, setLoading] = useState(false)
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const logoutFunction = (e) => {
        e.preventDefault();
        navigate('/admin')
        sessionStorage.clear()
    }
    
    useEffect(() =>{
        axios.get('https://crackitfindit.rad5.com.ng/api/transactions', { 
            headers: {
                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                Accept: 'application/json'
            }
         })
        .then(function (response) {
            // handle success
            setLoading(true)
            console.log(response.data.data)
            setTransactionArray(response.data.data)
            
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
        .then(function () {
            // always executed
        });
    }, [])

    

  return (
    <div>

    {
    
    loading ?

    <div className='transaction-component'>
        <Container className='transaction-general-container'>
            <Row>
                <Col className='col-side-bar' xs = {1}>
                        {/* <Button variant="primary" className="d-lg-none sidebar-btn" onClick={handleShow}>
                                Sidebar
                        </Button> */}
                        <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" />
                        <Offcanvas show={show} onHide={handleClose} responsive="lg">
                                <Offcanvas.Header closeButton>
                                <Offcanvas.Title></Offcanvas.Title>
                                </Offcanvas.Header>
                                <Offcanvas.Body>
                                <Col className = "users-side-bar" >
                        <Row>
                        <Link to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
                        </Row>   
                        <Row>
                        <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                            <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
                        </Row>
                                </Col>
                                </Offcanvas.Body>
                        </Offcanvas>
                </Col>
                <Col>
                <Container fluid className='transaction-table-cotainer'> 
                    <Table striped bordered hover responsive className='transaction-table' size="sm" >
                    <thead>
                    <tr>
                        <th>Transaction_Id</th>
                        <th>Curremcy</th>
                        <th>Amount</th>
                        <th>Status</th>
                        <th>Date Created</th>
                        <th>Name</th>
                        <th>Email</th>
                    </tr>
                    </thead>
                    <tbody>
                        {
                            transactionArray.map((transaction) => {
                                return(
                                    <tr>
                                    <td>{transaction.transactionId}</td>
                                    <td>{transaction.currency}</td>
                                    <td>{transaction.amount}</td>
                                    <td>{transaction.status}</td>
                                    <td>{transaction.date_created}</td>
                                    <td>{transaction.user.name}</td>
                                    <td>{transaction.user.email}</td>

                                </tr>
                                )
                            
                            
                            
                            

                            })
                            
                }
                    </tbody>
                </Table>
                <Button variant="secondary" className='me-5 '>Prev</Button>
                <Button variant="secondary" className='ms-5 '>Next</Button>
                </Container>
                </Col>
            </Row>
          
        </Container>
              

</div>
   :  <Container className='spinner'>
   <Row>
       <Col className = "text-center">
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
       </Col>
   </Row> 
   </Container>}

</div>
  
  )
}
