import Card from 'react-bootstrap/Card';
import React from 'react'
import ListGroup from 'react-bootstrap/ListGroup';
import {  Button } from "react-bootstrap"
import './formx.css'

export default function FormX() {
  return (
    <div>
        

        <Card className='formx-card'>
            <Card.Title>Hunt for the day</Card.Title>
            <Card.Body>
                <ListGroup className="list-group-flush">
                    <ListGroup.Item>Hunt for the day</ListGroup.Item>
                </ListGroup> 
            </Card.Body>

            <Card.Title>Hunt for the week</Card.Title>
            <Card.Body>
                <ListGroup className="list-group-flush">
                    <ListGroup.Item>Hunt for the week</ListGroup.Item>
                </ListGroup> 
            </Card.Body>
        </Card>    
    </div>
  )
}
