import Form from 'react-bootstrap/Form';
import Navbar from '../Navbar/Navbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import "./login.css";
import { Link } from "react-router-dom";
import { useState } from "react"
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
// import axios from 'axios';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';

function Login() {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [success, setSuccess] = useState(true);
    const [message, setMessage] = useState('');
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()
    let [network, setNetwork] = useState('')
    const api = 'https://crackitfindit.rad5.com.ng'
    const handleSubmit = (e) =>{
        e.preventDefault();
        setLoading(true)
        axios.post(api + '/api/admin-login', {
            'email': `${email}`,
            'password': `${password}`
          })
          .then(function (response) {
            setLoading(false)
            console.log(response.data.data.token);
            sessionStorage.setItem("Admin-Token", `${response.data.data.token}`);
            navigate("/admin-dashboard")
          })
          .catch(function (error) {
            if (error.response){
              setLoading(false)
              console.log(error.response.data);
              setSuccess(error.response.data.success)
              setMessage(error.response.data.message)
            }
            else{
              setLoading(false)
              setSuccess(false)
              setNetwork( network = 'Encountered an error, Please try again')
              console.log('error')
            }
          });

          
        
    }

  return (
    <div className='admin-login'>
        <Navbar />

        <Container>
          <Row>
          <Col lg={{ span: 6, offset: 3 }} md={{ span: 6, offset: 3 }} sm={{ span: 9, offset: 1 }} xs={{ span: 9, offset: 1 }}>
            <Form className = "admin-form" onSubmit={handleSubmit}>
            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Crack It, Find It</Form.Label> <br />
                <Form.Text className="text-muted">
                {success ?  null: <Alert style={{width : 'fit-content'}} key={'danger'} variant={'danger'} className = "admin_login_error_message">
                          {network == '' ? message : null}
                          {network}
                        </Alert>}
                </Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control type="email" placeholder="Enter email"  value={email}  onChange={(e) => setEmail(e.target.value)} />
              <Form.Text className="text-muted">
                We'll never share your email with anyone else.
              </Form.Text>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" placeholder="Password" value={password}  onChange={(e) => setPassword(e.target.value)}/>
            </Form.Group>
            <button className='btn btn-primary' >Login</button>
            {loading? <Spinner animation="border" role="status" className='spinner-admin'>
                                      <span className="visually-hidden">Loading...</span>
                                  </Spinner> : null}
            </Form>
          </Col>
          </Row>
        </Container>

        </div>
  );
}

export default Login;