import React from 'react'
import Table from 'react-bootstrap/Table';
import './transactionX.css'
import {Link} from 'react-router-dom'
import AdminNavbar from './AdminNavBar';
import { useState, useEffect } from 'react';
import axios from 'axios';
import Spinner from 'react-bootstrap/Spinner';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import logout from "../AdminAssests/logout.png";
import homeIcon from "../AdminAssests/home-icon.svg";
import pendingTransaction from "../AdminAssests/pending.png";
import theHunt from "../AdminAssests/archer.png";
import totalTransactions from "../AdminAssests/registration-complete.png";
import getHunt from "../AdminAssests/father-son-love.png";
import usersIcon from "../AdminAssests/users.png";
import sidebar from "../AdminAssests/global-navigation-bar.png";
import hunt from "../../Assests/hunt.jpg";
import Form from 'react-bootstrap/Form';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { useNavigate } from "react-router-dom";
import { useRef } from "react";

export default function Transaction(props) {
    const navigate = useNavigate()
    const [transactionArray, setTransactionArray] = useState('')
    const [loading, setLoading] = useState(false)
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [prevLink, setPrevLink] = useState('');
    const [nextLink, setNextLink] = useState('');
    const [disabled, setDisabled] = useState(true) 
    const [api, setApi] = useState('https://crackitfindit.rad5.com.ng/api/transactions')
    let count = 1;
    const logoutFunction = (e) => {
        e.preventDefault();
        navigate('/admin')
        sessionStorage.clear()
    }
    
    useEffect(() =>{
        axios.get(api, { 
            headers: {
                Authorization: "Bearer " + sessionStorage['Admin-Token'],
                Accept: 'application/json'
            }
         })
        .then(function (response) {
            // handle success
            setLoading(true)
            console.log(response.data.data)
            setTransactionArray(response.data.data)
            setPrevLink(response.data.links.prev)
            setNextLink(response.data.links.next)
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
        .then(function () {
            // always executed
        });
    }, [api])

    const prevBtn = (e) => {
        e.preventDefault()
        setApi(prevLink)
        
    }

    
    const nextBtn = (e) => {
        e.preventDefault()
        setApi(nextLink)
    }
    
    

  return (
    <div>

    {
    
    loading ?

    <div>
        <Container fluid>
            <Row>    
                <Col md = {1} className = 'transaction-offcanvas'>
                {/* <Button variant="primary" onClick={handleShow} className="d-lg-none">
                    Launch
                </Button> */}
                <img src={sidebar} onClick={handleShow}  className="d-lg-none sidebar-btn" alt="" />

                <Offcanvas show={show} onHide={handleClose} responsive="lg" >
                    <Offcanvas.Header closeButton>
                    <Offcanvas.Title>Responsive offcanvas</Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                    {/* <p className="mb-0">
                        This is content within an <code>.offcanvas-lg</code>.
                    </p> */}
                    <Col>
                    <Row>
                        <Link to='/'> <img src={hunt} alt="" className='side-bar-icon'  /></Link>
                    </Row>
                    <Row>
                        <Link to="/admin-dashboard"> <img src={homeIcon} alt="" className='side-bar-icon'  /></Link>
                    </Row>
                    <Row>
                        <Link to="/users"> <img src={usersIcon} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/total-transaction"> <img src={totalTransactions} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                        <Row>
                        <Link to="/hunt"> <img src={theHunt} alt="" className='side-bar-icon'  /></Link>
                        </Row> 
                        <Row>
                    <Link to="/hunts-table"> <img src={getHunt} alt="" className='side-bar-icon'  /></Link>
                    </Row>  
                        <Row>
                            <Link onClick={logoutFunction}> <img src={logout} alt="" className='side-bar-icon'  /></Link>
                        </Row>  
                    </Col>
                    </Offcanvas.Body>
                </Offcanvas>
                </Col>
                <Col>
                    <Row>
                        <Col className='text-center mt-3'>
                            <h4>List of Transactions</h4>
                        </Col>
                    </Row>
                    <Row>
                    <Table striped bordered hover responsive className='transaction-table mt-3' size="sm" >
                    <thead>
                    <tr>
                        <th>S/N</th>
                        <th>Transaction_Id</th>
                        <th>Currency</th>
                        <th>Amount</th>
                        <th>Status</th>
                        <th>Date Created</th>
                        <th>Name</th>
                        <th>Email</th>
                    </tr>
                    </thead>
                    <tbody>
                        {
                             
                            transactionArray.map((transaction) => {
                                return(
                                    <tr>
                                    <td>{count++}</td>
                                    <td>{transaction.transactionId}</td>
                                    <td>{transaction.currency}</td>
                                    <td>{transaction.amount}</td>
                                    <td>{transaction.status}</td>
                                    <td>{transaction.date_created}</td>
                                    <td>{transaction.user.name}</td>
                                    <td>{transaction.user.email}</td>
                                </tr>
                                )
                            })
                            
                }
                    </tbody>
                </Table>
                    </Row>
                    <Row className='text-center'>

                    {prevLink === null ? 
                   
                    <Col><Button variant="secondary" disabled = {disabled} className='me-5 mb-2  next-prev'>Prev</Button></Col>
                     :
                     <Col><Button variant="secondary" onClick={prevBtn} className='me-5 mb-2  next-prev'>Prev</Button></Col>
                    
                      }

                    {nextLink === null ? 
                    <Col><Button variant="secondary" disabled = {disabled} className='ms-5 mb-2 next-prev'>Next</Button></Col>
                     :
                     <Col><Button variant="secondary"  className='me-5 mb-2  next-prev'>Next</Button></Col>
                      }

                </Row>
                            
                            {/* <Col><Button variant="secondary" disabled = {disabled} onClick={prevBtn} className='me-5 mb-2  next-prev'>Prev</Button></Col>
                            <Col><Button variant="secondary" onClick={nextBtn} className='ms-5 mb-2 next-prev disabled'>Next</Button></Col>
                             */}
                   
                </Col>
            </Row>
          
        </Container>
              

</div>
   :  <Container className='spinner'>
   <Row>
       <Col className = "text-center">
           <Spinner animation="grow" role="status">
               <span className="visually-hidden">Loading...</span>
           </Spinner>
       </Col>
   </Row> 
   </Container>}

</div>
  
  )
}
