import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import hunt from "../Assests/hunt.jpg";

function CollapsibleExample() {
  return (
    <Navbar collapseOnSelect expand="lg" variant="dark" >
    <Container>
      <Navbar.Brand href="/hunted" className = 'brand'><img  
        src={hunt}   
        className="d-inline-block align-top"  
        alt="Brand logo"  
      />  </Navbar.Brand>
      {/* <Navbar.Toggle aria-controls="responsive-navbar-nav" /> */}
      {/* <Navbar.Collapse id="responsive-navbar-nav"> */}
        <Nav className='ms-auto'>
          {/* <Nav.Link className='nav-links' href="/hunted"><h6>ABOUT</h6></Nav.Link> */}
          <Nav.Link className='nav-links' href="/hunted/login"><h6>LOGIN</h6></Nav.Link>
        </Nav>
      {/* </Navbar.Collapse> */}
    </Container>
  </Navbar>
  );
}

export default CollapsibleExample;